import axios from 'axios' // axios
 
import Storage from './storage.js' // storage
 
 
 
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
 
 
 
/*  */
 
axios.interceptors.request.use(function (config) { // localStoragetoken
 
    let token = Storage.localGet('token')
 
    if (token) {
    
        token = 'bearer' + ' ' + token.replace(/'|"/g, '') // token
 
        config.headers.common['Authorization'] = token
 
    }
 
    return config
 
}, function (error) {
 
    return Promise.reject(error)
 
})
 
/*  */
 
axios.interceptors.response.use(function (response) { // 10010 token30 10011 token
 
if (response.data.code === 10010 || response.data.code === 10011) {
 
    Storage.localRemove('token') // token
 
    router.replace({
 
        path: '/land' // token
 
    })
 
} else if (response.data.token) { // tokentoken
 
    Storage.localSet('token', response.data.token) // token()
 
}
 
    return response
 
}, function (error) {
 
    return Promise.reject(error)
})


