<template>
<div class="cardBack">
    <div class="card-img" style="width:3.8rem;height:2.5rem; margin:.35rem auto;">
     <div class="bor" @click="onPickFilea"> 
        <input type="file"  ref="fileInputa" accept="imagea/*"  @change="getFilea" style="display: none" >           
        <img :src="imageUrla" :onerror="logo" :v-model="imageUrla"/> 
    </div>
</div>

</div> 
</template>
<script>
export default {
    data(){
        return{ 
            imagea:"",
            imageUrl: "",  //imgsrc
            imageUrla: "",  //imgsrc
            postUrl: "" ,   //
            idcardimg:'',  //
            logo: 'this.src="' + require('../../lib/img/ios/card-zheng@3x.png') + '"',
        }
    },
    methods:{
         // 
        //:, buttoninput
            onPickFilea () {
                 this.$refs.fileInputa.click()
            },
            //: ,     base64
            getFilea ($event) { 
                const files = $event.target.files
                let filename = files[0].name          //
                if ( filename.lastIndexOf('.') <= 0 ) {
                    return alert("Please add a valid image!")        //
                }
                const fileReader = new FileReader()                //new FileReader()   
                    fileReader.addEventListener('load',() => {
                    this.imageUrla = fileReader.result
                })
                    fileReader.readAsDataURL(files[0])
                    this.imagea = files[0]
                    //
                    // 
                    this.onUploada($event); 
            } ,
            //: 
            onUploada ($event) { 
                if(this.imageUrla!=''){
                    Toast("");
                }else{ 
                    $event.preventDefault();
                    let fd = new FormData();              //new FormData()  
                    fd.append('file',this.imagea);          //image 
                    var url="freeter-api/addIdcardFrontImg";                                  //(                  )
                    this.$http.post(url,fd).then( res => {                 //:this.postUrl
                        if(res.body.code==0){
                            this.idcardimg=res.body.idcardFrontImgUrl;  
                        } 
                 }) 
            } 
        },
    }
}
</script>
<style scoped>
@import '../../lib/css/login.css';
.title{
     font-size: 16px;
     font-weight: bold;
     font-family: ' PingFangSC-Regular, sans-serif';
     padding-left: .24rem;
     margin-bottom: .4rem;
 }
 .title span{
     color: #C5171F;
 }
 .bor img{
     width: 3.8rem;
     height: 2.5rem;
 }
 .card{
     position: relative;
 }
 .card img{
     width: 70%;
     margin-left: 15%;
 }
 .card span{
    font-size: 16px;
    color: #313131;
    font-family: 'SourceHanSerifSC-Light';
    position: absolute;
    left: 42%;
    top: 25%;
      
 } 
 #login .look .bor{
     margin-bottom: 3rem;
 }
 #login .look .inp{
     height: 1.9rem;bottom: 0;
 }
</style> 


