<template>
<div class="cardBack">
    <div class="card-img" style="width:3.8rem;height:2.5rem;margin:.35rem auto;">
        <div class="bor" @click="onPickFiles"> 
            <input type="file"  ref="fileInputs" accept="images/*"  @change="getFiles" style="display: none" >           
            <img :src="imageUrls" :onerror="cards" :v-model="imageUrls"/> 
        </div>
    </div>
</div>

</div> 
</template>
<script>
export default {
    data(){
        return{  
            images:"", 
            imageUrls: "",  //imgsrc
            idback: "" ,   // 
            postUrl: "" , 
            cards:  'this.src="' + require('../../lib/img/ios/card-fan@3x.png') + '"',
        }
    },
    methods:{
       // 
        //:, buttoninput
            onPickFiles() {
                 this.$refs.fileInputs.click()
            },
            //: ,     base64
            getFiles ($event) { 
                const files = $event.target.files
                let filename = files[0].name          //
                if ( filename.lastIndexOf('.') <= 0 ) {
                    return alert("Please add a valid image!")        //
                }
                const fileReader = new FileReader()                //new FileReader()   
                    fileReader.addEventListener('load',() => {
                    this.imageUrls = fileReader.result
                })
                    fileReader.readAsDataURL(files[0])
                    this.images = files[0]
                    //
                    // 
                    this.onUploads($event); 
            } ,
            //: 
            onUploads ($event) { 
                if(this.imageUrls!=''){
                    Toast("");
                }else{ 
                    $event.preventDefault();
                    let fd = new FormData();              //new FormData()  
                    fd.append('file',this.images);          //image 
                    var url="freeter-api/addIdcardBackImg";                                  //(                  )
                    this.$http.post(url,fd).then( res => {                 //:this.postUrl
                        if(res.body.code==0){
                            this.idback=res.body.idcardBackImgUrl;  
                        } 
                 }) 
            }  
        },
    }
}
</script>
<style scoped>
@import '../../lib/css/login.css';
.title{
     font-size: 16px;
     font-weight: bold;
     font-family: ' PingFangSC-Regular, sans-serif';
     padding-left: .24rem;
     margin-bottom: .4rem;
 }
 .title span{
     color: #C5171F;
 }
 .bor img{
     width: 3.8rem;
     height: 2.5rem;
 }
 .card{
     position: relative;
 }
 .card img{
     width: 70%;
     margin-left: 15%;
 }
 .card span{
    font-size: 16px;
    color: #313131;
    font-family: 'SourceHanSerifSC-Light';
    position: absolute;
    left: 42%;
    top: 25%;
      
 } 
 #login .look .bor{
     margin-bottom: 3rem;
 }
 #login .look .inp{
     height: 1.9rem;bottom: 0;
 }
</style> 


