<template>
    <div id="map">
        <router-link to="/life">
         <div class="top" style="z-index:4;position:fixed;top:0; width:100%;">
            <div class="top-left"><img src="../../lib/img/ios/fanhui@3x.png" alt=""></div> 
            <div class="top-right">{{lifename}}</div>
        </div>
        </router-link>
        <div class="map">
            {{arrive}}
        </div>
    </div>
</template>
<script>
var map,marker;
export default {
    data(){
        return{
           lifename:this.$route.params.name, 
            arriveCoor:[108.947025,34.2613255],//
            arrive:"",//
        }
    },
    mounted() {   
    mapDraw(this.arriveCoor),
    mapCoor(this.arriveCoor)
  },
  methods:{
     mapDraw(arriveCoor){
     map = new AMap.Map('map-location', {  //map-locationdivid
       resizeEnable: true, //
       zoom:16, //
       center: arriveCoor //
     });
     // 
     this.addMarker(arriveCoor);
  },
  // 
  addMarker(arriveCoor) {
    var _this = this;
    marker = new AMap.Marker({
      icon: "", //ip
      imageSize: "20px",
      position: arriveCoor,
      offset: new AMap.Pixel(-13, -30),
      // 
      draggable: true,
      cursor: 'move',
      // 
      raiseOnDrag: true
    });
    marker.setMap(map);
  },
 // 
 mapCoor(lnglatXY){
var _this = this;
AMap.service('AMap.Geocoder',function() {//
var geocoder = new AMap.Geocoder({});
geocoder.getAddress(lnglatXY, function (status, result) {
if (status === 'complete' && result.info === 'OK') {
//:
_this.arrive = result.regeocode.formattedAddress;
}else {
_this.arrive = "";
}
});
})
}
},
}
</script>

<style scoped>
   @import '../../lib/css/top.css'; 
   #map{
       width: 100%;
       height: 100%;
       position: absolute;
       background: #fff;
   }
   .map{
       width: 96%;
       height: 10rem;
       margin:1rem auto;
       background: beige;
   }
</style>


