<template>
    <div id="detail">
        <router-link to="/home">
         <div class="top" style="z-index:4;position:fixed;top:0;border-bottom:1px solid #eee;width:100%;">
            <div class="top-left"><img src="../../lib/img/ios/fanhui@3x.png" alt=""></div>
            <div class="top-right"></div>
        </div>
        </router-link>
        <!-- <div class="title"><router-link to="/home"><img src="../../lib/img/ios/fanhuizuo@2x.png" alt="" style="width:.24rem;float:left;margin-left:.24rem;margin-top:.25rem;"></router-link> </div> -->
        <div class="content">
            <div style="margin-left:.4rem;"></div>
            <div style="margin-left:.4rem;"></div>
            <div style="margin-left:.4rem;"></div>
            <div style="margin-left:.4rem;margin-right:.24rem;text-indent:.8rem;">,</div>
            <div style="margin-left:.4rem;margin-right:.24rem;text-indent:.8rem;"> </div>
            <div style="margin-left:.4rem;text-indent:.8rem;"></div>
            <div style="margin-left:.4rem;"></div>
            <div style="margin-left:.4rem;margin-right:.24rem;text-indent:.8rem;">1956</div>
            <div style="margin-left:.4rem;margin-right:.24rem;text-indent:.8rem;">--</div>
            <div style="margin-left:.4rem;margin-right:.24rem;text-indent:.8rem;">1990500</div>
            <div style="margin-left:.4rem;margin-right:.24rem;text-indent:.8rem;">2017</div>
        </div>
        <div id="ban">
            Copyright    {{year}} All Right Reservid
        </div>
    </div>  
</template>
<script>
export default {
    data(){
        return{
            year:0,
        }
    }, 
    created(){
        this.getdate();
    },
    methods:{
        getdate(){
           var date=new Date();
           this.year=date.getFullYear(); 
        },
    },
    //
    watch: {
        '$route.path':function (newVal,oldVal) {
            console.log(newVal,oldVal);
        } 
    }
}
</script>
<style scoped>
.top{
    height: 1rem; 
    line-height: 1rem;
    background: #fff;
    padding-left: .24rem;
}
.top div{
    float: left;
}
.top .top-left{
    width: .2rem;
}
.top .top-left img{
    width: 100%;
    width: .2rem;
    height: .35rem;
   margin-top: .3rem;
   position: fixed; 
}
.top .top-right{
    width: 96%; 
    text-align: center;
    font-size: .42rem;
    font-weight: bold;
    color: #313131;
    font-family:"Source Han Sans CN";
}
.router-link-active{
    background: none;
}
#detail .title{
    width: 100%;
    font-size: .42rem;
    text-align: center;
    font-family: 'SourceHanSerifSC-Light';
    font-weight: bold;
    height:1rem;
    line-height:1rem; 
    margin-bottom: .2rem; 
}
.content{
    line-height: .8rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
    background: #fff;
}
#ban{
    width: 6rem;
    margin: auto;
    text-align: center;
    color: #313131;
    border: 2px dashed #939393;
    margin-bottom: 1rem;
    background: #fff;
    border-top-left-radius: .2rem;
    border-top-right-radius: .2rem;
    border-bottom-left-radius: .2rem;
    border-bottom-right-radius: .2rem;
}
</style>



