<template>
<div id="login">
    <div class="no-back">
    <div class="land-top">
        <span class="top-img">
            <router-link to="/land" class="fd">
            <img src="../../lib/img/ios/fanhuileft@3x.png" alt="">
            <span></span> 
            </router-link></span> 
    </div> 
     <div class="content" style="width:100%;padding-left:.25rem;padding-right:.25rem;">
         <div class="tel name-bor">
            <div class="tel-left"><span>*</span></div>
                <div class="con" style="width:61%;margin-left:30%;">
                <input type="text" v-model="name" placeholder="">
                </div>
            <div class="right-img">
                <img src="../../lib/img/ios/fanhuiyou@3x.png" alt="">
            </div> 
        </div>
        <div class="tel sex-bor">
            <div class="tel-left"></div>
            <div class="con" style="width:61%;margin-left:30%;">
                <div class="boy">
                    <input type="radio" name="radios" value="1" v-model="param"><label></label>
                </div> 
                <div class="girl">
                    <input type="radio" name="radios" value="0" v-model="param"><label></label>
                </div>
            </div>
            <div class="right-img">
                <img src="../../lib/img/ios/fanhuiyou@3x.png" alt="">
            </div>
        </div>  
         
         <div class="tel" style="width:100%;height:1.3rem; ">
             <div class="tel-left" style="float:left;"><span>*</span></div>
            <div class="con" style="width:61%;margin-left:30%;">
                <input type="text" v-model="phone" placeholder="" oninput="if(value.length>11)value=value.slice(0,11)">
            </div>
            <div class="right-img">
                <img src="../../lib/img/ios/fanhuiyou@3x.png" alt="">
            </div>
         </div>
         <div class="tel yz" style="margin-top:.01rem;">
            <div class="tel-left"><span>*</span></div>
            <div class="con" style="width:61%;margin-left:30%;color:#939393;">
                <input type="text" v-model="codes" style="width:60%; height:100%;color:#939393;" placeholder=""  oninput = "value=value.replace(/[^\d]/g,'')">
                <span  @click="getcode" style="width:35%;font-size:.24rem;height:.8rem;margin-top:.25rem;line-height:.8rem; text-align:center; border:1px solid #e1e1e1; float:right;"></span>
            </div>
            <!-- <input type="text" placeholder=""> -->
            <div class="right-img">
                <img src="../../lib/img/ios/fanhuiyou@3x.png" alt="">
            </div>
         </div> 
         <div class="tel psword" style="">
            <div class="tel-left"><span>*</span></div>
                <div class="con" style="width:61%;margin-left:30%;">
                <input type="password" maxlength="12" minlength="6" onkeyup="value=value.replace(/[^a-za-z0-9u4e00-u9fa5]/g,'')" v-model="pass" placeholder="6~12">
                </div>
            <div class="right-img">
                <img src="../../lib/img/ios/fanhuiyou@3x.png" alt="">
            </div> 
        </div> 
        <!--<div class="tel" style="border-bottom:1px solid #e5e5e5;">
            <div class="tel-left"><span>*</span></div>
                <div class="con" style="width:61%;margin-left:30%;">
                <input type="text" v-model="code" maxlength="13" placeholder=""  oninput="if(value.length>14)value=value.slice(0,13)">
                </div>
            <div class="right-img">
                <img src="../../lib/img/ios/fanhuiyou@3x.png" alt="">
            </div> 
        </div>-->
         
         
        <!-- <div class="tel" >
            <div class="tel-left"><span>*</span></div>
            <div class="con" style="width:61%;margin-left:30%;">
                <input type="text"  id="idCode" v-model="card" maxlength="18" placeholder=""> 
            </div>
            <div class="right-img">
                <img src="../../lib/img/ios/fanhuiyou@3x.png" alt="">
            </div>
        </div> --> 
        <div class="tel select-address" >
            <div class="tel-left"><span>*</span></div>
            <div class="con" style="width:55%;margin-left:30%;">
                <input type="text"  v-model="city" placeholder="" onkeyup="value=value.replace(/[^a-za-z0-9u4e00-u9fa5]/g,'')"  > 
            </div>
            <div class="right-img" style="width:15%;">
                <div @click="toAddress" style="color:#c5171f;font-size:.28rem;float:right;"></div>
            </div>
        </div>
        <div class="tel" style="border:none;">
            <div class="tel-left"><span>*</span></div>
            <div class="con" style="width:70%;margin-left:30%;">
                <input type="text"  v-model="detailcity" placeholder=""  > 
            </div> 
        </div>
     </div>
     <div class="look"> 
        <div class="xieyi"><span><input type="checkbox" v-bind:checked="isChecked" v-on:click="handleDisabled"/></span><router-link to="/agreement"></router-link></div>
        <div class="inp">
            <div class="etc" v-if="etcShow" @click="postclick()"></div>
            <div class="etc noshow" v-else></div> 
            <!-- <router-link to="/logina" style="color:#fff;"> --> 
            <!-- </router-link> -->
            <!-- <div class="etc" v-else style="background:#eee;color:#C5171F;"><img src="../../lib/img/ios/shenheloginright@3x.png" alt=""> </div> -->
        </div>
    </div> 
</div>
 <div style="" class="mask" v-show="mask"> 
    <v-distpicker type="mobile" @selected='selected' v-show="addInp" static-placeholder></v-distpicker>
 </div>  
</div>  
</template>
<script>
import {Rem} from "../../lib/js/rem.js";
import {source} from '../../lib/js/SourceHanSansCN-Regular.ttf' 
import {Pingf} from '../../lib/js/pingfang.ttf' 
import VDistpicker from 'v-distpicker'
import {Toast} from 'mint-ui'
function filterInput(val) {
// 
    return val.replace(/[^A-z0-9]/, '')
};
function filtercard(val) {
    // return val.replace(/[^A-z0-9]/, '')
}
export default { 
    name: 'whatever',
    data(){
        return{ 
            phone:'', 
            codes:'',  
            pass:'', 
            code:'',  
            etcShow:false,
            isChecked:false,
            detailcity:'',
            param: '0',
            city:'', 
            name:'',
            province:'',
            citys:'',
            areas:'',
            addInp :false,
            mask:false,
        }

    },  
    components: { VDistpicker }, 
    methods:{
        ifs(){
            var code=this.codes;
            if(code === null){
                Toast("");
            }
        }, 
        toAddress(){
            this.mask = true;
            this.addInp = true; 
            console.log('1111111');
        },
        getcode(){
           var phone=this.phone;
           if(!/^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/.test(this.phone)){
               Toast('')
           }else{
            var url=`freeter-api/login/sendCode?phone=${phone}`;
            this.$http.post(url).then(res=>{ 
                if(res.body.code==0){
                    Toast("");
                }else if(res.body.code==500){
                    Toast("")
                }
            })
           }
            
        },  
        //methods
        handleDisabled:function(){
            this.isChecked = !this.isChecked;
            if(this.isChecked==true){
                this.isDisabled =  true;
                this.etcShow=true;
            }
            else{
                this.isDisabled =  false;
                this.etcShow=false;
            }
             console.log(this.isChecked)
        },
        // 
        // next(){
        //     var phone=this.phone;
        //     var codes=this.codes;
        //     var pass=this.pass;
        //     var code=this.code;
        //     if(phone=='' || codes=='' || pass=='' || code == ''){
        //         Toast("")
        //     }else{
        //         this.$router.push({name:'logina',params:{phone,codes,pass,code}}); 
        //     } 
        // }, 
         // 
        selected(data){
            this.mask =false;
            this.addInp = false;
            this.city = data.province.value + ' ' + data.city.value +' ' + data.area.value;
            this.province= data.province.value;
            this.citys=data.city.value;
            this.areas=data.area.value; 
        }, 
        postclick(){ 
            var phone=this.phone; var name=this.name; 
            var code=this.codes; var sex=this.param;
            var province=this.province; var citys=this.citys;
            var areas=this.areas; var pass=this.pass; 
            var codepwd=this.code; var detailcity=this.detailcity;
            var url=`freeter-api/register?phone=${phone}&phoneCode=${code}&address=${detailcity}&sex=${sex}&userName=${name}&province=${province}&city=${citys}&county=${areas}&password=${pass}`;
            if(detailcity=='' ||province==''||citys==''||areas==''){
                Toast('');
            }else if(phone==''){
                Toast('');
            }else if(name==''){
                Toast(""); 
            }else if(sex==''){
                Toast("");
            }else if(code==''){ 
                Toast("");
            }else if(pass == ""){
                Toast("");   
            }else{
                this.$http.post(url).then(res=>{
                    console.log(url);
                    if(res.body.code==0){
                        Toast('');
                        this.$router.push("/land"); 
                    }else if(res.body.code==500){
                        Toast(res.body.msg);
                    }
                })
            }
        },
    },
    watch:{
        pass(val){
            this.$nextTick(()=>{
                this.pass=filterInput(val);
            })
        }, 
    } 
}
</script>
<style scoped>
 @import '../../lib/css/login.css';
 .name-bor,.sex-bor,.psword,.select-address{
       border-bottom:1px solid #e5e5e5; 
 } 
 .look{
     margin-top: .3rem;
 }
#login .look .inp{
     margin-top: .3rem;
 } 
 .xieyi{width: 60%;margin: auto;height: .5rem;position: relative;}
 .xieyi span{position: absolute;top: .02rem;}
 .xieyi input[type=checkbox]{width: 15px;height: 15px;margin-top:.1; }
.xieyi a{line-height: .5rem;margin-left: .2rem;position: absolute;top: -0.05rem;left:.3rem;font-size: .32rem;color:#C5171F;}
.noshow{background: #e5e5e5 !important;color: #989898;}  
 
</style>



